.container {
  display: flex;
  min-height: 100vh;
  background-color: #fff;
  padding-top: calc(var(--navbar-height));
}
.image_container {
  display: none;
}

.form_container {
  flex: 1;
  display: flex;
  box-sizing: border-box;
  padding: 0.5rem 1rem;
}

.form {
  display: flex;
  flex-direction: column;
  min-width: 45%;
  height: fit-content;
  padding: 2em 3em;
  margin: clamp(2rem, 5%, 5rem) auto;
  border-radius: 1rem;
  background-color: #cde7fbe1;
  color: #285fa4;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  gap: 1rem;
  font-size: 1rem;
}

.form_header {
  font-size: 1em;
  text-align: center;
}
.form_body {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  font-size: 1em;
}
.form_body form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-block: 1rem;
}
/* .form_body form p{
  width: 100%;
  text-align: center;
} */
.form_input_container {
  display: flex;
  width: 100%;
}
.form_input_container > * {
  width: 100%;
  padding: 1em;
  border-radius: 0.5rem;
  border: none;
  font-size: 1em;
  font-weight: 400;
  color: rgb(94, 93, 93);
}
.form_input_container > *:focus {
  outline: none;
}

.form_input_container > input::placeholder {
  color: rgb(94, 93, 93);
}

.form_actions_container {
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: 1em;
  align-items: center;
  justify-content: space-between;
  margin-block-start: 1rem;
}

.form_actions_container > * {
  font-size: 1em;
  font-weight: bold;
  color: #285fa4;
}

.form_actions_container button {
  padding: 0.8em 4em;
  border-radius: 0.5rem;
  border: none;
  background-color: #285fa4;
  color: #fff;
  font-size: 1em;
  font-weight: 600;
  letter-spacing: 0.1em;
}

.form_actions_container > *:hover {
  cursor: pointer;
}

.form_vactions_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
}

.form_vactions_container > * {
  font-size: 1em;
  font-weight: bold;
  color: #285fa4;
}
.form_vactions_container > *:hover {
  cursor: pointer;
  outline: none;
}

.form_vactions_container > p:nth-child(1) {
  font-size: 1em;
  font-weight: 400;
  color: rgb(94, 93, 93);
}
.form_vactions_container > p:nth-child(1) span {
  font-size: 1em;
  font-weight: bold;
  color: #285fa4;
}
.form hr {
  box-sizing: border-box;
  border: none;
  border-top: 1px solid #285fa4;
  width: 100%;
}

.form_footer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-block-start: 0.5rem;
  font-size: 1em;
  font-weight: 400;
  color: rgb(94, 93, 93);
}
.form_footer a {
  color: #285fa4;
  font-weight: 600;
}

@media (min-width: 768px) {
  .image_container {
    position: relative;
    flex: 0.7;
    display: flex;
    background: #cde7fb;
    margin-block-start: 3vh;
    border-radius: 0 5rem 5rem 0;
  }
  .image_container img {
    top: 7%;
    right: -30%;
    position: absolute;
    max-width: 90%;
    max-height: 70%;
    object-fit: cover;
  }
  .form_container {
    padding-inline-start: clamp(2rem, 20%, 10rem);
  }
  .form {
    margin-inline-start: clamp(1rem, 15%, 7rem);
    margin-right: 1rem;
    font-size: 0.8rem;
  }
}
